
.app-header {
   -webkit-box-shadow: 0px 1px 2px rgba(23, 61, 99, 0.15);
           box-shadow: 0px 1px 2px rgba(23, 61, 99, 0.15);
   padding: 10px 0;
   margin-bottom: 24px;
}
.app-logo {
   height:38px;
}
.user {
 padding-top:8px;
}

.projects-table tbody tr:hover {
  background: #F0F2F4;
  cursor: pointer;
}

.bread-crumbs {
  list-style: none;
  margin: 0;
}
.bread-crumbs li {
  display: inline-block;
  margin: 0;
  padding: 0;
}
h1 {
  margin-bottom: 24px;
}

.tool-card {
  color: #173D63;
  background: #FFFFFF;
  -webkit-box-shadow: 0px 1px 2px rgba(23, 61, 99, 0.15);
          box-shadow: 0px 1px 2px rgba(23, 61, 99, 0.15);
  border-radius: 5px;
  padding: 20px 30px;
}
.tool-card:hover {
  color: #173D63;
  background: #69E2B2;
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
  -webkit-box-shadow: 0 1px 1px -1px rgba(23, 61, 99,.33), 0 10px 35px -5px rgba(105, 226, 178,.4), 0 15px 20px -15px rgba(23, 61, 99,.15);
          box-shadow: 0 1px 1px -1px rgba(23, 61, 99,.33), 0 10px 35px -5px rgba(105, 226, 178,.4), 0 15px 20px -15px rgba(23, 61, 99,.15);
}
.tool-card {
  font-size: 14px;
}

.project-info {
  list-style: none;
  margin: 0 0 24px 0;
}
.project-info li {
  display: inline-block;
  margin: 0;
  padding: 0 18px 0 0;
}
















































































































































































































































































































































































































































































































































































































































































































































